@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff2") format(woff2), url("../fonts/opensans.woff") format(woff);
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2") format(woff2), url("../fonts/opensansbold.woff") format(woff);
  font-display: swap;
}
.button {
  box-sizing: border-box;
  display: inline-flex;
  font-family: inherit;
  margin: 0;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: #444444;
  border: 2px solid #444444;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 700;
  cursor: pointer;
  background-color: transparent;
}
.button--primary {
  line-height: 20px;
}
.button--secondary {
  line-height: 16px;
}

.input {
  font-family: inherit;
  font-size: 17px;
  line-height: 18px;
  color: #000000;
  border-bottom: 2px solid #000000;
  border: none;
  background: transparent;
  box-sizing: border-box;
  box-shadow: inset 0 -2px 0;
}

.radio__label {
  display: flex;
  align-items: center;
}

.radio__mark {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 42px;
  min-width: 42px;
  border: 2px solid #444444;
  border-radius: 100%;
  margin-right: 19px;
  box-sizing: border-box;
  flex-shrink: 0;
}

.radio__title {
  font-family: inherit;
  font-size: 17px;
  font-weight: 700;
  line-height: 18px;
  color: #000000;
}

.radio_input:checked + .radio__mark::before {
  background-color: #62d1ba;
  content: "";
  width: 18px;
  height: 18px;
  border-radius: 100%;
}

.radio:not(:first-of-type) {
  margin-top: 11px;
}

.checkbox__label {
  display: flex;
  align-items: center;
}

.checkbox__mark {
  display: flex;
  justify-content: center;
  align-items: center;
  content: "";
  border: 2px solid #444444;
  min-height: 38px;
  min-width: 38px;
  margin-right: 23px;
  box-sizing: border-box;
  flex-shrink: 0;
}

.checkbox__title {
  font-family: inherit;
  font-size: 17px;
  font-weight: 700;
  line-height: 18px;
  color: #000000;
}

.checkbox__input:checked + .checkbox__mark::before {
  content: "";
  width: 23px;
  height: 16px;
  background-image: url("../img/checkbox.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.visually-hidden {
  position: absolute;
  max-width: 1px;
  max-height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

